.App {
  font-family: Arial, sans-serif;
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.App-header {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

.Form-input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

.Form-button {
  width: 100%;
  padding: 12px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.Form-button:hover {
  background-color: #45a049;
}

.People-list {
  list-style: none;
  padding: 0;
}

.Person-item {
  background-color: white;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Person-info {
  font-size: 18px;
}

.Button-delete,
.Button-update {
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 8px 15px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.Button-update {
  background-color: #2196f3;
  margin-left: 10px;
}

.Button-delete:hover,
.Button-update:hover {
  background-color: #d32f2f;
}
